* {
  box-sizing: border-box;
}

body {
  width: 100%;
  border: 0px;
  font-size: 22px;
  font-family: 'Courier New', Courier, monospace;
  background-color: #e6e6e6;
}

.App {
 max-width: 768px;
 margin: 0 auto;
}

/* Home Page  */
.main-conatiner {
  width: 100%;
}

.main-bg {
  padding: 10px;
}
.game-set {
  width: 100%;
  margin-top: 10px;
  text-align: center;
  background-color: whitesmoke;
  border-radius: 10px;
  float: left;
}

.game-set button {
  font-size: 20px;
  width: 50px;
  height: 40px;
  margin: 5px;
  background-color: #f6c700;
  border-style: none;
  border-radius: 5px;
}

.game-set .start-btn {
  width: 200px;
  height: 50px;
  display: inline-block;
}

.game-set select {
  font-size: 20px;
  width: 60px;
  height: 40px;
  margin: 5px;
  text-align: center;

  border-style: none;
}

.home-profile-container {
  box-sizing: border-box;
  height: 100px;
  border-radius: 10px;
  padding: 10px;
}

.home-profile {
  width: 25%;
  height: 130px;
  float: left;
}

.home-profile img {
  width: 60px;
  height: 60px;
  padding: 8px;
  border-radius: 100px;
  background-color: #f6c700;;
}

.home-profile input {
  width: 100px;
  height: 24px;
  font-size: 16px;
  text-align: center;
  border-style: none;
  border-radius: 10px;
  background-color: lightskyblue;
}

/* Play Page */
/* About Scoreboard  */
.main-container .menu {
  width: 100%;
  margin: 0 auto;
}

.icon {
  padding: 10px;
  display: inline-block;
  text-align: center;
  font-size: 26px;
  width: 8%;
  /*eight: 40px;*/
  margin: 5px;
  background-color :#e6e6e6;
  border: 0.5px solid black;
  border-radius: 5px;
}

.board-container {
  width: 99%;
  height: 160px;
  /* background-color: rgb(239, 239, 239); */
  /* border: 2px solid #f6c700; */
  background-color: whitesmoke;
  border-radius: 7px;
  margin: 0 auto;
  margin-bottom: 1px;
  padding: 5px;
  border-bottom: 3px solid lightblue;
  border-right: 3px solid lightblue;
}

.board-profile {
  width: 25%;
  height: 130px;
  float: left;
  padding: 2px;
}

.board-profile img {
  width: 50px;
  height: 50px;
  padding: 8px;
  border-radius: 100px;
  background-color: #f6c700;
}

.board-profile .name {
  margin-left: 2%;
  width: 66%;
  height: 18px;
  font-size: 12px;
  text-align: center;
  border-style: none;
  border-radius: 5px;
  background-color: lightskyblue;
}

.board-profile .rank {
  width: 20%;
  height: 18px;
  font-size: 16px;
  color: black;
  text-align: left;
  border-style: none;
  /* border-radius: 10px; */
  border-left: 8px solid #f6c700;
  background-color: #e6e6e6;
}

.board-profile .score {
  text-align: center;
  font-size: 50px;
  border-radius: 5px;
  margin: 0 auto;
  width: 80%;
  height: 50px;
  background-color: lightblue;
  color: white;
}

.board-msg {
  width: 81%;
  height: 50px;
  display: inline-block;
  /*margin: 0 auto; */
  text-align: center;

  font-size: 1.1rem;
  border: 0.5px solid black;
  border-radius: 5px;
}

.board-msg h3 {
  margin: 10px 0px 0px 0px;
}

/**************************************/

/* About Card */
.card-container {
  padding: 0.25%;
  margin: 0 auto;
}

.card {
  transition: all 0.6s;
  transform: rotateY(-180deg);
  /*backface-visibility: visible;*/
  width: 12%;
  height: 110px;
  float: left;
  background-color: #f6c700;
  border-radius: 5px;
  border: 0.5px solid black;
  margin: 0.25%;
}

/*ホバーアクションをPCだけにしたい場合、←のメディアクエリで囲む*/
@media (hover: hover) and (pointer: fine) {
  .icon:hover {
    background-color: #f6c700;
  }
  .card:hover {
    background-color: brown;
  }
}


.heart {
  text-align: center;
  color: white;
  padding-top: 35px;
}

/*
.card:hover {
  border: 2px red solid;
  background-color: rgb(128, 73, 0);
}
*/

.selected-card {
  transition: all 0.6s;
  transform: rotateY(0deg);
  backface-visibility: hidden;
  width: 12%;
  height: 110px;
  float: left;
  background-color: aquamarine;
  border: 0.5px solid black;
  border-radius: 5px;
  /* border: 1px green solid; */
  margin: 0.25%;
}

.selected-heart {
  display: none;
}

