.card-img {
  width: 90%;
  height: 90%;
  margin: 5%;
  display: none;
}
.card-img-back {
  width: 100%;
  height: 100%;
  border-radius: 5px;
}

.selected-card-img {
  width: 90%;
  height: 90%;
  margin: 5%;
}

/* inactive cards */
.inactive-card {
  transition: all 2s;
  transform: rotateY(3600deg);
  width: 12%;
  /* height: 100px; */
  height: 110px;
  float: left;
  background-color: #e6e6e6;
  border: 0.5px dotted black;
  border-radius: 7px;
  /*border-color: white;*/
  margin: 0.25%;
}

.inactive-card-img {
  transition: all 1.9s;
  opacity: 0.5;
  width: 90%;
  height: 90%;
  margin: 5%;
  visibility: hidden;
}

div, button, select, img {
  cursor: pointer;
}

/* css of Modal of poketmon list */
.mpl-layout {
  position: fixed;
  left: 0px;
  top: 2%;
  width: 100%;
  height: 100%;
}

.mpl-back-cover {
  position: fixed;
  top: 0px;
  left: 0px;
  background-color: black;
  opacity: 0.65;
  width: 100%;
  height: 100%;
}

.mpl-container {
  position: relative;
  max-width: 728px;
  width: 80%;
  height: 96%;
  margin: 0 auto;
  border-radius: 5px;
  background-color: whitesmoke;
  overflow: scroll;
}

.rapper-btn-close {
  position: relative;
  top: -1%;
  max-width: 728px;
  width: 80%;
  height: 0;
  margin: 0 auto;
}

.rapper-btn-close .btn-close {
  position: relative;
  top: 0%;
  left: 97%;
  background-color: #f6c700;
  width:40px;
  height:40px;
  font-size: 30px;
  border-radius: 30px;
  text-align: center; 
  z-index: 2;
}

@media (hover: hover) and (pointer: fine) {
  .rapper-btn-close .btn-close:hover {
    color: #f6c700;
    background-color: brown;
  }
}

.pokemon-list {
  transform: 1s all;
  transition: padding-left(100px);
  position: relative;
  top: 250px;
  width: 98%;
  height: 10%;
  margin: 10px auto;
  padding: 3px;
  border-radius: 5px;
  border: solid 1px lightgray;
  border-left: solid 8px #f6c700;
}

.pokemon-list-img {
  width: 15%;
  height: 90%;
  margin: 5px;
}
/* about search board */
.search-board {
  position: fixed;
  top: 2%;
  left: auto;
  height: 250px;
  background-color: whitesmoke;
  width: 80%;
  max-width: 728px;
  margin: 0 auto;
  border-radius: 5px;
  z-index: 1;
  font-size: 1rem;
  overflow: hidden;
}

.search-board .search-box {
  position: relative;
  display: block;
  margin: 0 auto;
  width: 60%;
  height: 40px;
  border-radius: 5px;
  font-size: 1.2em;
  border-style: none;
  padding: 8px;
}

.search-board h3 {
  width: 50%;
  margin: 0 auto;
  margin-top: 5px;
  text-align: center;
  color: brown;
  font-size: 1.5rem;
}